























import { Component, Vue } from 'vue-property-decorator';
import { CaptainsLogClass } from './model/CaptainsLogClasses';
import LostBattleArray from './model/LostBattleArray';
import ManPwrArray from './model/ManPwrArray';

interface WarriorFileOutput {
  captainsLog: {[index: string]: {[index: number]: string}};
  lostBattleArray: string[];
  manPwrArray: {[index: string]: string};
}

@Component
export default class CaptainsLog extends Vue {
  private remoteServer: string = localStorage.getItem('remoteServer') || '';

  private remoteServerChanged() {
    localStorage.setItem('remoteServer', this.remoteServer);
  }

  get captainsLog(): CaptainsLogClass
  {
    return this.$store.state.captainsLog;
  }

  get lostBattleArray(): LostBattleArray
  {
    return this.$store.state.lostBattleArray;
  }

  get manPwrArray(): ManPwrArray
  {
    return this.$store.state.manPwrArray;
  }

  private async getOutputToSave(): Promise<WarriorFileOutput> {
    return { 
      captainsLog: await this.captainsLog.getStoredValue(),
      lostBattleArray: await this.lostBattleArray.getStoredValue(), 
      manPwrArray: await this.manPwrArray.getStoredValue() 
    };
  }

  private async onLocalSaveClicked()
  {
    let outputObj: WarriorFileOutput = await this.getOutputToSave();
    let output = JSON.stringify(outputObj);
    let blob = new Blob([output], {type: 'application/json'});
    let url = URL.createObjectURL(blob);

    let a = document.createElement('a');
    a.href = url;
    a.download = "warrior.warriorApp";
    a.textContent = "Download warrior.warriorApp";
    a.onclick = () => { document.body.removeChild(a); }
    a.style.display = "none";

    document.body.appendChild(a);
    a.click();
  }

  private loadInput(input: WarriorFileOutput) 
  {
    this.captainsLog.setStoredValue(input.captainsLog);
    this.lostBattleArray.setStoredValue(input.lostBattleArray);
    this.manPwrArray.setStoredValue(input.manPwrArray);
  }

  private onLocalFileLoadClicked(evt) {
    let files = evt.target.files;

    let fileReader = new FileReader();
    fileReader.onload = (fileLoadedEvent: any) => {
      let output: WarriorFileOutput = JSON.parse(fileLoadedEvent.target.result);
      this.loadInput(output);
    }
    fileReader.readAsText(files[0], "UTF-8");
  }

  private snackbarMessage: string = '';

  private showSnackbar(text: string)
  {
    this.snackbarMessage = text;
    let x = document.getElementById("snackbar");
    x.className = "show";
    setTimeout(() => { x.className = x.className.replace("show", ""); }, 3000);
  }

  private async onRemoteSaveClicked()
  {
    try
    {
      await fetch(`${this.remoteServer}/warrior`, {
        method: 'POST',
        mode: 'cors',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(await this.getOutputToSave())
      });

      this.showSnackbar('Save successful');
    }
    catch(e)
    {
      console.log('Error');
      this.showSnackbar('Save Error');
    }
  }

  private async onRemoteLoadClicked()
  {
    try
    {
      let data = await fetch(`${this.remoteServer}/warrior`);
      this.loadInput(await data.json());
      this.showSnackbar('Load successful');
    }
    catch(e)
    {
      console.log('Error');
      this.showSnackbar('Load Error');
    }
  }

  private async mounted() {
    document.addEventListener('show-toaster', (elem) => this.showSnackbar(elem.detail));
  }
}
